import { ChangeDetectionStrategy, Component, EventEmitter, Input, OnInit, Output } from '@angular/core';

import icAdd from '@iconify/icons-ic/twotone-add';
import icHistory from '@iconify/icons-ic/twotone-history';
import icLabel from '@iconify/icons-ic/twotone-label';
import icStar from '@iconify/icons-ic/twotone-star';
import icViewHeadline from '@iconify/icons-ic/twotone-view-headline';

import { Icon } from '@visurel/iconify-angular';

import { fadeInRight400ms } from '@vex/animations/fade-in-right.animation';
import { stagger40ms } from '@vex/animations/stagger.animation';

import { FilterEvent } from '@data/interfaces';
import { Scenario } from '@data/schema/scenario';
import { ScenariosIndexPaginatorService } from '@scenarios/pages/scenarios-index/services/scenarios-index-paginator.service';

export interface ScenariosTableMenu {
  type: 'link' | 'subheading';
  id?: 'recent' | 'starred' | 'all' | 'high' | 'medium' | 'low' | 'none';
  icon?: Icon;
  label: string;
  classes?: {
    icon?: string;
  };
}

@Component({
  selector: 'scenarios-table-menu',
  templateUrl: './scenarios-table-menu.component.html',
  animations: [fadeInRight400ms, stagger40ms],
  changeDetection: ChangeDetectionStrategy.OnPush,
})
export class ScenariosTableMenuComponent implements OnInit {
  @Input() initialFilter: ScenariosTableMenu['id'];
  @Input() items: ScenariosTableMenu[] = [
    {
      type: 'link',
      id: 'all',
      icon: icViewHeadline,
      label: 'All',
    },
    // {
    //   type: 'link',
    //   id: 'recent',
    //   icon: icHistory,
    //   label: 'Recent'
    // },
    {
      type: 'link',
      id: 'starred',
      icon: icStar,
      label: 'Starred',
    },
    {
      type: 'subheading',
      label: 'Priority',
    },
    {
      type: 'link',
      id: 'high',
      icon: icLabel,
      label: 'High',
      classes: {
        icon: 'text-primary-500',
      },
    },
    {
      type: 'link',
      id: 'medium',
      icon: icLabel,
      label: 'Medium',
      classes: {
        icon: 'text-green-500',
      },
    },
    {
      type: 'link',
      id: 'low',
      icon: icLabel,
      label: 'Low',
      classes: {
        icon: 'text-amber-500',
      },
    },
    {
      type: 'link',
      id: 'none',
      icon: icLabel,
      label: 'None',
      classes: {
        icon: 'text-gray-500',
      },
    },
  ];

  @Output() filter = new EventEmitter<FilterEvent>();
  @Output() create = new EventEmitter<void>();

  pageSize: number;
  activeCategory: ScenariosTableMenu['id'] = 'all';

  // Icons
  icAdd = icAdd;

  constructor(
    private scenariosIndexPaginatorService: ScenariosIndexPaginatorService
  ) { }

  ngOnInit() {
    this.scenariosIndexPaginatorService.pageSize.subscribe(pageSize => {
      this.pageSize = pageSize;
    });
  }

  isActive(category: ScenariosTableMenu['id']) {
    return this.activeCategory === category;
  }

  setFilter(category: ScenariosTableMenu['id']) {
    this.activeCategory = category;

    const event: FilterEvent = { filter: undefined };

    switch (category) {
      case 'all':
        event.filter = undefined;
        break;
      case 'starred':
        event.filter = 'starred';
        break;
      case 'recent':
        event.filter = 'updated_at';
        break;
      case 'high':
        event.filter = 'high_priority';
        break;
      case 'medium':
        event.filter = 'medium_priority';
        break;
      case 'low':
        event.filter = 'low_priority';
        break;
      case 'none':
        event.filter = 'none_priority';
        break;
    }

    this.filter.emit(event);
  }
}
