import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';

import { ProjectResolver } from '@projects/services/project-resolver.service';
import { ScenarioDetailsComponent } from '@scenarios/pages/scenario-details/scenario-details.component';
import { ScenariosBuilderComponent } from '@scenarios/pages/scenarios-builder/scenarios-builder.component';
import { ScenariosIndexComponent } from '@scenarios/pages/scenarios-index/scenarios-index.component';
import { RequestsResolver } from '@scenarios/services/requests-resolver.service';
import { ScenarioResolver } from '@scenarios/services/scenario-resolver.service';
import { ScenariosResolver } from '@scenarios/services/scenarios-resolver.service';

import { RequestDetailsComponent } from '@requests/pages/request-details/request-details.component';
import { RequestResolver } from '@requests/services/request-resolver.service';
import { RequestsAvailableResolver } from '@requests/services/requests-available-resolver.service';
import { ResponseHeadersResolver } from '@requests/services/response-headers-resolver.service';
import { ResponseResolver } from '@requests/services/response-resolver.service';

const routes: Routes = [
  {
    path: '',
    component: ScenariosIndexComponent,
    resolve: {
      scenarios: ScenariosResolver,
      project: ProjectResolver,
    },
  },
  {
    path: ':scenario_id',
    component: ScenarioDetailsComponent,
    resolve: {
      scenario: ScenarioResolver,
      requests: RequestsResolver,
    },
  },
  {
    path: ':scenario_id/editor',
    component: ScenariosBuilderComponent,
    resolve: {
      project: ProjectResolver,
      scenario: ScenarioResolver,
      requests: RequestsResolver,
      requestsAvailable: RequestsAvailableResolver,
    },
  },
  {
    path: ':scenario_id/requests/:request_id',
    component: RequestDetailsComponent,
    resolve: {
      parentResource: ScenarioResolver,
      request: RequestResolver,
      response: ResponseResolver,
      responseHeaders: ResponseHeadersResolver,
    },
  },
];

@NgModule({
  imports: [
    RouterModule.forChild(routes),
  ],
  exports: [RouterModule],
})
export class ScenariosRoutingModule { }
