import { Location } from '@angular/common';
import { AfterViewInit, Component, Input, OnDestroy, OnInit } from '@angular/core';
import { FormControl } from '@angular/forms';
import { MatDialog } from '@angular/material/dialog';
import { MatFormFieldDefaultOptions, MAT_FORM_FIELD_DEFAULT_OPTIONS } from '@angular/material/form-field';
import { MatSelectChange } from '@angular/material/select';
import { ActivatedRoute, ActivatedRouteSnapshot, Router } from '@angular/router';

import { fadeInUp400ms, stagger40ms  } from '@vex/animations';
import { Breadcrumb } from '@vex/interfaces/breadcrumb.interface';
import { TableColumn } from '@vex/interfaces/table-column.interface';

import { RequestResource } from '@core/http/request-resource.service';
import { FileService } from '@core/utils/file.service';
import { RequestIndexResponse } from '@data/interfaces';
import { Project, Request, Scenario } from '@data/schema';
import { ProjectDataService } from '@projects/services/project-data.service';
import { ScrumboardDialogComponent } from '@requests/components/scrumboard-dialog/scrumboard-dialog.component';

import { RequestsCreateComponent } from './components/requests-create/requests-create.component';
import { RequestsDataService } from './services/requests-data.service';
import { ScenarioDetailsIcons } from './services/scenario-details-icons.service';

@Component({
  selector: 'scenario-details',
  templateUrl: './scenario-details.component.html',
  styleUrls: ['./scenario-details.component.scss'],
  animations: [
    fadeInUp400ms,
    stagger40ms,
  ],
  providers: [
    {
      provide: MAT_FORM_FIELD_DEFAULT_OPTIONS,
      useValue: {
        appearance: 'standard',
      } as MatFormFieldDefaultOptions,
    },
  ],
})
export class ScenarioDetailsComponent implements OnInit, AfterViewInit, OnDestroy {
  layoutCtrl = new FormControl('boxed');
  crumbs: Breadcrumb[] = [];

  projectId: Project['id'];
  scenario: Scenario;
  requests: Request[];
  indexParams: any = {};

  // Table
  @Input()
  tableColumns: TableColumn<Request>[];
  totalRequests: number;
  page = 0;
  pageSize = 10;
  filter: string;

  constructor(
    public icons: ScenarioDetailsIcons,
    private activatedRoute: ActivatedRoute,
    private dialog: MatDialog,
    private location: Location,
    private file: FileService,
    private projectDataService: ProjectDataService,
    private requestResource: RequestResource,
    private requestsDataService: RequestsDataService,
    private route: ActivatedRoute,
    private router: Router
  ) { }

  ngOnInit() {
    const routeSnapshot =  this.route.snapshot;

    this.projectId = routeSnapshot.queryParams.project_id;
    this.scenario = routeSnapshot.data.scenario;

    const requests = routeSnapshot.data.requests;
    this.requests = requests.list;
    this.totalRequests = requests.total;
    this.requestsDataService.set(this.requests.map(request => {
      return new Request(request);
    }));

    this.indexParams = { ...routeSnapshot.queryParams };
    this.indexParams.page = this.indexParams.page || 0;
    this.indexParams.size = this.indexParams.size || 20;
    this.page = this.indexParams.page;
    this.pageSize = this.indexParams.size;
    this.filter = this.indexParams.filter;

    this.buildBreadcrumbs();
    this.tableColumns = this.buildTableColumns();
  }

  ngAfterViewInit() {}

// API Access

  createRequest(data: FormData) {
    const snapshot: ActivatedRouteSnapshot = this.route.snapshot;
    const project_id = snapshot.queryParams.project_id;

    data.append('project_id', project_id);

    this.requestResource.create(data).subscribe(
      (res: Request) => {
        const clone = this.requests.slice();
        clone.unshift(res);
        this.requests = clone;
      },
      error => {

      });
  }

  getRequests(params: any = { ...this.indexParams }) {
    const snapshot: ActivatedRouteSnapshot = this.route.snapshot;
    const project_id = snapshot.queryParams.project_id;

    params.project_id = project_id;
    params.scenario_id = this.scenario.id;

    this.requestResource.index(params).subscribe(
      (res: RequestIndexResponse) => {
        this.requests = res.list;
        this.totalRequests = res.total;

        this.requestsDataService.set(this.requests.map(request => {
          return new Request(request);
        }));

        delete params.scenario_id;
        this.updateUrlQueryParams(params);
      },
      error => {

      }
    );
  }

  destroyRequest(requestId: Request['id']) {
    this.requestResource.destroy(requestId).subscribe(res => {
      const clone = this.requests.filter((request: Request) => {
        return request.id !== requestId;
      });

      for (let i = 0; i < clone.length; ++i) {
        clone[i].position = i;
      }

      this.requests = clone;
    });
  }

  sortRequests(event) {
    const column: string = event.active;
    const direction: string = event.direction; // desc or asc

    if (!direction) {
      delete this.indexParams.sort_by;
      delete this.indexParams.sort_order;
    } else {
      this.indexParams.sort_by = column;
      this.indexParams.sort_order = direction;
    }

    this.getRequests();
  }

  searchRequests(queryString: string) {
    const snapshot: ActivatedRouteSnapshot = this.route.snapshot;
    const project_id = snapshot.queryParams.project_id;

    this.indexParams.page = 0;
    this.page = 0;

    if (!queryString.length) {
      delete this.indexParams.q;
    } else {
      this.indexParams.q = queryString;
    }

    this.getRequests();
  }

// View Access

  viewRequest(requestId: Request['id']) {
    const request = this.requests.find(r => {
      return r.id === requestId;
    });

    this.dialog.open(ScrumboardDialogComponent, {
      data: { request },
      width: '750px',
      maxWidth: '100%',
      disableClose: true,
    });
  }

  editRequest(requestId: Request['id']) {
    const path: string = this.file.join(location.pathname, '/requests', requestId);
    const snapshot: ActivatedRouteSnapshot = this.route.snapshot;

    this.router.navigate([path], {
       queryParams: { project_id: snapshot.queryParams.project_id },
    });
  }

  showEndpoint(request: Request) {
    const path: string = this.file.join('/endpoints', request.rollupId);
    const snapshot: ActivatedRouteSnapshot = this.route.snapshot;

    this.router.navigate([path], {
       queryParams: { project_id: snapshot.queryParams.project_id },
    });
  }

  openCreateDialog() {
    const dialogRef = this.dialog.open(RequestsCreateComponent, {
      width: '600px',
    });

    const onCreateSub = dialogRef.componentInstance.onCreate.subscribe(($event) => {
      this.createRequest($event);
    });

    dialogRef.afterClosed().subscribe((request: Request) => {
      onCreateSub.unsubscribe();
    });
  }

  showBuilder() {
    const snapshot: ActivatedRouteSnapshot = this.route.snapshot;
    const path = `${location.pathname}/editor`;

    this.router.navigate([path], {
       queryParams: { project_id: snapshot.queryParams.project_id },
    });
  }

  onComponentChange(change: MatSelectChange, row: Request) {
    const requests = this.requestsDataService.requests;
    const index = requests.findIndex(c => c === row);
    requests[index].components = change.value;
    this.requestsDataService.set(requests);
  }

 /*
  *
  * $event = {
  *   previousPageIndex: 0
  *   pageIndex: 0
  *   pageSize: 50
  *   length: 3
  * }
  *
  */
  handlePaginateChange($event) {
    const curIndex = this.page;
    const curSize = this.pageSize;
    const newIndex = $event.pageIndex;
    const newSize = $event.pageSize;

    if (curSize !== newSize) {
      this.pageSize = newSize;
    }

    if (curIndex != newIndex) {
      this.page = newIndex;
    }

    if (curIndex != newIndex || curSize !== newSize) {
      this.indexParams.page = newIndex;
      this.indexParams.size = newSize;
      this.getRequests();
    }
  }

// Helpers

  updateUrlQueryParams(newQueryParams) {
    const queryParams = { ...this.indexParams };

    Object.entries(newQueryParams).forEach(([key, value]) => {
      queryParams[key] = value;
    });

    const url = this
        .router
        .createUrlTree([], {relativeTo: this.activatedRoute, queryParams})
        .toString();

    this.location.go(url);
  }

  projectIdQuery() {
    return {project_id: this.route.snapshot.queryParams.project_id};
  }

  endpointPath(request: Request) {
    if (!request.rollupId) { return ''; }
    return this.file.join('/endpoints', request.rollupId);
  }

  buildBreadcrumbs() {
    // Build breadcrumb
    if (this.projectDataService.project) {
      this.crumbs.push({
        name: this.projectDataService.project.name,
      });
    } else {
      const o = this.projectDataService.project$.subscribe((project: Project) => {
        if (project) {
          this.crumbs.unshift({
            name: project.name,
          });

          o.unsubscribe();
        }
      });
      this.projectDataService.fetch(this.projectId);
    }

    this.crumbs.push({
      name: 'Scenarios',
      routerLink: ['/scenarios'],
      queryParams: this.route.snapshot.queryParams,
    });
    this.crumbs.push({ name: this.scenario.name });
  }

  buildTableColumns(): TableColumn<Request>[] {
    return [
      { label: 'Checkbox', property: 'checkbox', type: 'checkbox', visible: true, canHide: false },
      { label: 'Position', property: 'position', type: 'custom', visible: true, canHide: true, cssClasses: ['text-secondary'] },
      { label: 'Method', property: 'method', type: 'text', visible: true, canHide: true, cssClasses: ['text-secondary'] },
      { label: 'URL', property: 'url', type: 'text', visible: true, canHide: true, cssClasses: ['font-medium'] },
      { label: 'Endpoint', property: 'endpoint', type: 'link', visible: true, canHide: true,
        routerLink: (request: Request) => {
          return [this.endpointPath(request)];
        },
        queryParams: () => {
          return this.projectIdQuery();
        },
      },
      { label: 'Status', property: 'status', type: 'custom', visible: true, canHide: true },
      { label: 'Latency', property: 'latency', type: 'custom', visible: true, canHide: true },
      { label: 'Components', property: 'components', type: 'custom', visible: false, canHide: true },
      { label: 'Created At', property: 'created_at', type: 'date', visible: false, canHide: true, cssClasses: ['text-secondary'] },
      {
        label: '',
        property: 'menu',
        type: 'menuButton',
        cssClasses: ['text-secondary', 'w-10'],
        visible: true,
        canHide: false,
      },
    ];
  }

  ngOnDestroy() { }
}
