import { CdkDragDrop, moveItemInArray, transferArrayItem } from '@angular/cdk/drag-drop';
import { Component, OnInit, TemplateRef } from '@angular/core';
import { FormControl } from '@angular/forms';
import { MatDialog } from '@angular/material/dialog';
import { ActivatedRoute, ActivatedRouteSnapshot } from '@angular/router';

import { debounceTime, filter, map } from 'rxjs/operators';

import icClose from '@iconify/icons-ic/twotone-close';

import { fadeInUp400ms, stagger80ms } from '@vex/animations';
import { PopoverService } from '@vex/components/popover/popover.service';
import { trackById } from '@vex/utils/track-by';

import { RequestResource } from '@core/http/request-resource.service';
import { RequestIndexResponse } from '@data/interfaces';
import { Project, Request, Scenario } from '@data/schema';
import { ScrumboardDialogComponent } from '@requests/components/scrumboard-dialog/scrumboard-dialog.component';

import { ScrumboardCard } from './interfaces/scrumboard-card.interface';
import { ScrumboardList } from './interfaces/scrumboard-list.interface';
import { Scrumboard } from './interfaces/scrumboard.interface';
import { RequestCardAdapterService } from './services/request-card-adapter.service';
import { ScenariosBuilderIcons } from './services/scenarios-builder-icons.service';

@Component({
  selector: 'vex-scenarios-builder',
  templateUrl: './scenarios-builder.component.html',
  styleUrls: ['./scenarios-builder.component.scss'],
  animations: [
    stagger80ms,
    fadeInUp400ms,
  ],
})
export class ScenariosBuilderComponent implements OnInit {
  layoutCtrl = new FormControl('fullwidth');

  SCENARIO_CONTAINER_ID = 1;
  REQUESTS_CONTAINER_ID = 2;

  crumbs = [];

  project: Project;
  scenario: Scenario;
  requests: Request[];
  requestsAvailable: Request[];

  board: Scrumboard = {
    id: 1,
    label: '',
    children: [],
  };
  scenarioRequestsList: ScrumboardList = {
    id: this.SCENARIO_CONTAINER_ID,
    label: 'Requests',
    children: [],
  };
  availableRequestsList: ScrumboardList = {
    id: this.REQUESTS_CONTAINER_ID,
    label: 'Available Requests',
    children: [],
  };

  // this.route.paramMap.pipe(
  //   map(paramMap => +paramMap.get('scrumboardId')),
  //   map(scrumboardId => scrumboards.find(board => board.id === 1))
  // );

  addCardCtrl = new FormControl();
  addListCtrl = new FormControl();

  showAddRequests = false;

  icClose = icClose;

  constructor(
    private dialog: MatDialog,
    private route: ActivatedRoute,
    private popover: PopoverService,
    private requestCardAdapterService: RequestCardAdapterService,
    private requestResource: RequestResource,
    private icons: ScenariosBuilderIcons
  ) { }

  ngOnInit() {
    this.scenario = new Scenario(this.route.snapshot.data.scenario);
    this.project = new Project(this.route.snapshot.data.project);

    this.board.label = this.scenario.name;

    this.board.children.push(this.scenarioRequestsList);
    this.board.children.push(this.availableRequestsList);

    this.buildRequests();
    this.buildAvailableRequests(this.route.snapshot.data.requestsAvailable);

    this.buildCrumbs();
  }

  searchAvailableRequests(queryString: String) {
    const snapshot: ActivatedRouteSnapshot = this.route.snapshot;
    const project_id = snapshot.queryParams.project_id;
    const query = {
      project_id,
      scenario_id: -1,
      q: queryString,
      page: this.availableRequestsList.search.page,
      size: this.availableRequestsList.search.size,
    };

    this.requestResource.index(query).subscribe((res: RequestIndexResponse) => {
      this.buildAvailableRequests(res);

      this.availableRequestsList.search.total = res.total;
      this.availableRequestsList.search.q = queryString;
    });
  }

// View Access

  open(board: Scrumboard, list: ScrumboardList, card: ScrumboardCard) {
    // this.addCardCtrl.setValue(null);

    let request = this.requests.find(request => request.id === card.id);

    if (!request) {
      request = this.requestsAvailable.find(request => request.id === card.id);
    }

    this.dialog.open(ScrumboardDialogComponent, {
      data: { request },
      width: '750px',
      maxWidth: '100%',
      disableClose: true,
    }).beforeClosed().pipe(
      filter<ScrumboardCard>(Boolean)
    ).subscribe(value => {
      // Do something after dialog is closed
    });
  }

  /*
   * Enable dropping requests/endpoints into only scenario container
   *
   */
  drop(event: CdkDragDrop<ScrumboardCard[]>) {
    if (event.container.id !== this.SCENARIO_CONTAINER_ID.toString()) { return; }

    const requestId = event.item.data.id;
    const nextPosition = event.currentIndex;

    if (event.previousContainer === event.container) {
      // If moving position of request in scenario

      const request = this.requests.find(request => request.id === requestId);
      const curPosition = request.position;

      this.requestResource.update(request.id, {
        position: nextPosition,
      }).subscribe(res => {
        const r1 = this.requests[curPosition];
        const r2 = this.requests[nextPosition];

        r1.position = nextPosition;
        r2.position = curPosition;

        this.buildRequests();
      });

      moveItemInArray(
        event.container.data,
        event.previousIndex,
        event.currentIndex
      );
    } else {
      // If moving request from availableRequests

      this.requestResource.update(event.item.data.id, {
        scenario_id: this.scenario.id,
        position: nextPosition,
      }).subscribe(res => {
        const request = this.requestsAvailable.find(request => request.id === requestId);
        request.position = nextPosition;

        this.requests.splice(nextPosition, 0 , request);

        for (let i = 0; i < this.requests.length; ++i) {
          this.requests[i].position = i;
        }

        this.buildRequests();
      });

      transferArrayItem(
        event.previousContainer.data,
        event.container.data,
        event.previousIndex,
        event.currentIndex
      );
    }
  }

  toggleStar(board: Scrumboard) {
    board.starred = !board.starred;
  }

  toggleAddRequests() {
    this.showAddRequests = !this.showAddRequests;
  }

// Helpers

  getConnectedList(board: Scrumboard) {
    return board.children.map(x => `${x.id}`);
  }

  buildCrumbs() {
    this.crumbs.push({name: this.project.name});
    this.crumbs.push({
      name: 'Scenarios',
      routerLink: ['/scenarios'],
      queryParams: this.route.snapshot.queryParams,
    });
    this.crumbs.push({
      name: this.scenario.name,
      routerLink: [`/scenarios/${this.scenario.id}`],
      queryParams: this.route.snapshot.queryParams,
    });
    this.crumbs.push({name: 'Editor'});
  }

  buildRequests() {
    const requestsData = this.route.snapshot.data.requests;
    this.requests = requestsData.list;
    this.requests.sort((a, b) => a.position - b.position);

    this.buildListChildren(this.scenarioRequestsList, this.requests);
  }

  buildAvailableRequests(requests) {
    const _this = this;
    const requestsAvailableData = requests;
    this.requestsAvailable = requestsAvailableData.list;
    this.buildListChildren(this.availableRequestsList, requestsAvailableData.list);

    this.availableRequestsList.search = {
      q: '',
      page: 0,
      size: 25,
      total: requestsAvailableData.total,
      // ctrl: new FormControl(),
      label: 'Search Requests...',
      // get str$() {
      //   return this.ctrl.valueChanges.pipe(
      //     debounceTime(1000)
      //   )
      // },
      onScroll() {
        this.page += 1;
        _this.searchAvailableRequests(this.q || '');
      },
    };

    // this.availableRequestsList.search.str$.subscribe(this.searchAvailableRequests.bind(this));
  }

  buildListChildren(list, requests) {
    list.children = requests.map(request => {
      const scrumboardCard: ScrumboardCard = this.requestCardAdapterService.createCard(new Request(request));
      return scrumboardCard;
    });
  }
}
