import { Injectable } from '@angular/core';

import { BehaviorSubject } from 'rxjs';

import { Request } from '@data/schema/request';

@Injectable({
  providedIn: 'root',
})
export class RequestsDataService {
  private subject = new BehaviorSubject([]);
  requests$ = this.subject.asObservable();
  requests: Request[];

  constructor() { }

  set(requests: Request[]) {
    this.requests = requests;
    this.subject.next(requests);
  }

  add(request: Request) {
    this.requests.unshift(new Request(request));
    this.set(this.requests);
  }

  delete(id: number) {
    this.requests.splice(
      this.requests.findIndex((existingRequest) => {
        return existingRequest.id === id;
      }), 1);

    this.set(this.requests);
  }
}
