import { Component, Inject, Input, OnInit } from '@angular/core';
import { MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';

import { DateTime } from 'luxon';

import icAdd from '@iconify/icons-ic/twotone-add';
import icAssignment from '@iconify/icons-ic/twotone-assignment';
import icAttachFile from '@iconify/icons-ic/twotone-attach-file';
import icClose from '@iconify/icons-ic/twotone-close';
import icDelete from '@iconify/icons-ic/twotone-delete';
import icDescription from '@iconify/icons-ic/twotone-description';
import icImage from '@iconify/icons-ic/twotone-image';
import icInsertComment from '@iconify/icons-ic/twotone-insert-comment';
import icMoreVert from '@iconify/icons-ic/twotone-more-vert';
import icStar from '@iconify/icons-ic/twotone-star';

import { Observable, of } from 'rxjs';

import { BodyParamResource } from '@core/http/body-param-resource.service';
import { HeaderResource } from '@core/http/header-resource.service';
import { QueryParamResource } from '@core/http/query-param-resource.service';
import { ResponseResource } from '@core/http/response-resource.service';
import { Project, Request } from '@data/schema';

@Component({
  selector: 'vex-scrumboard-dialog',
  templateUrl: './scrumboard-dialog.component.html',
  styleUrls: ['./scrumboard-dialog.component.scss'],
})
export class ScrumboardDialogComponent implements OnInit {
  request: Request;

  components: Array<any> = [];
  response: any;

  icAssignment = icAssignment;
  icDescription = icDescription;
  icClose = icClose;
  icAdd = icAdd;
  icMoreVert = icMoreVert;
  icDelete = icDelete;
  icImage = icImage;
  icAttachFile = icAttachFile;
  icInsertComment = icInsertComment;
  icStar = icStar;

  constructor(
    @Inject(MAT_DIALOG_DATA) private data: {
      request: Request
    },
    private bodyParamResource: BodyParamResource,
    private dialogRef: MatDialogRef<ScrumboardDialogComponent>,
    private headerResource: HeaderResource,
    private queryParamResource: QueryParamResource,
    private responseResource: ResponseResource
  ) { }

  ngOnInit() {
    this.request = new Request(this.data.request);

    const defaultQueryParams = {
      project_id: this.request.projectId,
    };

    const bodyParams$ = this.bodyParamResource.index(this.request.id, defaultQueryParams);
    const headers$ = this.headerResource.index(this.request.id, defaultQueryParams);
    const queryParams$ = this.queryParamResource.index(this.request.id, defaultQueryParams);
    const response$ = this.responseResource.index(this.request.id, defaultQueryParams);

    this.components = [
      {
        title: 'Headers',
        data$: headers$,
      },
      {
        title: 'Query Params',
        data$: queryParams$,
      },
      {
          title: 'Body Params',
          data$: bodyParams$,
      },
    ];

    this.response = {
      title: 'Response',
      data$: response$,
      accessed: true,
    };
  }

  handleTabChange($event) {
    const index = $event.index - 1;

    if (index >= 0) {
      this.components[index].accessed = true;
    }
  }

// Helpers

  prettyJson(json) {
    try {
      return JSON.stringify(JSON.parse(json), null, 2);
    } catch (err) {
      return json;
    }
  }
}
