import { SelectionModel } from '@angular/cdk/collections';
import { AfterViewInit, Component, EventEmitter, Input, OnInit, Output, SimpleChanges, ViewChild } from '@angular/core';
import { FormControl } from '@angular/forms';
import { MatFormFieldDefaultOptions, MAT_FORM_FIELD_DEFAULT_OPTIONS } from '@angular/material/form-field';
import { MatPaginator } from '@angular/material/paginator';
import { MatSort } from '@angular/material/sort';
import { MatTableDataSource } from '@angular/material/table';

import { debounceTime } from 'rxjs/operators';

import icCloudDownload from '@iconify/icons-ic/twotone-cloud-download';
import icDelete from '@iconify/icons-ic/twotone-delete';
import icDeleteForever from '@iconify/icons-ic/twotone-delete-forever';
import icEdit from '@iconify/icons-ic/twotone-edit';
import icFilterList from '@iconify/icons-ic/twotone-filter-list';
import icMoreVert from '@iconify/icons-ic/twotone-more-vert';
import icSearch from '@iconify/icons-ic/twotone-search';
import icStar from '@iconify/icons-ic/twotone-star';
import icStarBorder from '@iconify/icons-ic/twotone-star-border';

import { fadeInUp400ms } from '@vex/animations/fade-in-up.animation';
import { scaleFadeIn400ms } from '@vex/animations/scale-fade-in.animation';
import { stagger20ms } from '@vex/animations/stagger.animation';
import { TableColumn } from '@vex/interfaces/table-column.interface';

import { Scenario } from '@data/schema/scenario';
import { ScenariosIndexPaginatorService } from '@scenarios/pages/scenarios-index/services/scenarios-index-paginator.service';

@Component({
  selector: 'scenarios-data-table',
  templateUrl: './scenarios-data-table.component.html',
  styleUrls: ['./scenarios-data-table.component.scss'],
  providers: [
    {
      provide: MAT_FORM_FIELD_DEFAULT_OPTIONS,
      useValue: {
        appearance: 'standard',
      } as MatFormFieldDefaultOptions,
    },
  ],
  animations: [
    stagger20ms,
    fadeInUp400ms,
    scaleFadeIn400ms,
  ],
})
export class ScenariosDataTableComponent<T> implements OnInit, AfterViewInit {

  @Input() set data(value: T[]) {
    this.dataSource.data = value;
  }
  @Input() columns: TableColumn<T>[];
  @Input() pageSize = 20;
  @Input() pageSizeOptions = [10, 20, 50];
  @Input() searchStr: string;

  @Output() toggleStar = new EventEmitter<Scenario['id']>();
  @Output() edit = new EventEmitter<Scenario['id']>();
  @Output() delete = new EventEmitter<Scenario['id']>();
  @Output() view = new EventEmitter<Scenario['id']>();
  @Output() download = new EventEmitter<Scenario['id']>();

  // Search
  searchCtrl = new FormControl();
  searchStr$ = this.searchCtrl.valueChanges.pipe(
    debounceTime(1000)
  );

  dataSource = new MatTableDataSource<T>();
  selection = new SelectionModel<T>(true, []);

  @ViewChild(MatPaginator, { static: true }) paginator: MatPaginator;
  @ViewChild(MatSort, { static: true }) sort: MatSort;

  icMoreVert = icMoreVert;
  icStar = icStar;
  icStarBorder = icStarBorder;
  icDeleteForever = icDeleteForever;
  icEdit = icEdit;
  icCloudDownload = icCloudDownload;
  icSearch = icSearch;
  icDelete = icDelete;
  icFilterList = icFilterList;

  constructor(
    private scenariosIndexPaginatorService: ScenariosIndexPaginatorService
  ) { }

  ngOnInit() {
    this.searchCtrl.patchValue(this.searchStr);
    this.searchStr$.subscribe($event => {
      this.dataSource.filter = ($event || '').trim().toLowerCase();
    });
  }

  get visibleColumns(): Array<keyof T | string> {
    return this.columns.filter(column => column.visible).map(column => column.property);
  }

  // ngOnChanges(changes: SimpleChanges): void {
  //   if (changes.columns) {
  //     return this.columns.filter(column => column.visible).map(column => column.property);
  //   }
  // }

  ngAfterViewInit() {
    this.dataSource.paginator = this.paginator;
    this.dataSource.sort = this.sort;
  }

// Emit Event

  emitToggleStar(event: Event, id: Scenario['id']) {
    event.stopPropagation();
    this.toggleStar.emit(id);
  }

  removeSelected(scenarios: Scenario[]) {
    scenarios.forEach(s => this.delete.emit(s.id));

    this.selection.clear();
  }

// View Access

  toggleColumnVisibility(event, index: number, column) {
    event.stopPropagation();
    event.stopImmediatePropagation();

    this.columns[index].visible = !this.columns[index].visible;
  }

  onPaginateChange($event) {
    /*
      previousPageIndex: 0
      pageIndex: 0
      pageSize: 50
      length: 3
    */
    this.scenariosIndexPaginatorService.changePageSize($event.pageSize);
  }

  /** Selects all rows if they are not all selected; otherwise clear selection. */
  masterToggle($event) {
    $event.preventDefault();

    if (this.isAllSelected()) {
      this.unselectAll();
    } else {
      if (this.isPartiallySelected()) {
        // If current page has something selected, then clear current page
        // Else select everything in current page

        let hasSelected = false;
        this.dataSource.data.some(row => {
          if (this.isSelected(row)) {
            hasSelected = true;
            return true;
          }
        });

        if (hasSelected) {
          this.unselectAll();
        } else {
          this.selectAll();
        }
      } else {
        this.selectAll();
      }
    }
  }

// Helpers

  /** Whether the number of selected elements matches the total number of rows. */
  isAllSelected() {
    if (!this.selection.hasValue()) {
      return false;
    }

    const numSelected = this.selection.selected.length;
    const numRows = this.dataSource.data.length;
    return numSelected === numRows;
  }

  isPartiallySelected() {
    return this.selection.hasValue() && !this.isAllSelected();
  }

  isSelected(row: T) {
    return this.selection.isSelected(row);
  }

  selectAll() {
    this.dataSource.data.forEach(row => this.selection.select(row));
  }

  unselectAll() {
    this.selection.clear();
  }
}
