import { Component, EventEmitter, Inject, OnInit } from '@angular/core';
import { FormBuilder, FormControl, Validators } from '@angular/forms';
import { MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';

import icClose from '@iconify/icons-ic/twotone-close';
import icDownload from '@iconify/icons-ic/twotone-cloud-download';
import icDelete from '@iconify/icons-ic/twotone-delete';
import icLabel from '@iconify/icons-ic/twotone-label';
import icMoreVert from '@iconify/icons-ic/twotone-more-vert';
import icPrint from '@iconify/icons-ic/twotone-print';
import icTitle from '@iconify/icons-ic/twotone-title';

import { AliasDiscovery } from '@core/utils/alias-discovery.service';
import { ScenarioPriorities, ScenarioPriorityData } from '@data/schema';

@Component({
  selector: 'scenarios-create',
  templateUrl: './scenarios-create.component.html',
  styleUrls: ['./scenarios-create.component.scss'],
})
export class ScenariosCreateComponent implements OnInit {
  // ngx-dropzone
  files: File[] = [];

  form = this.fb.group({
    name: new FormControl('', [Validators.required]),
    description: '',
  });

  // icons
  icMoreVert = icMoreVert;
  icClose = icClose;
  icPrint = icPrint;
  icDownload = icDownload;
  icDelete = icDelete;
  icTitle = icTitle;
  icLabel = icLabel;

  onCreate = new EventEmitter<object>();

  scenarioPriorities = ScenarioPriorityData;
  selectedPriority = this.scenarioPriorities[3];

  constructor(
    @Inject(MAT_DIALOG_DATA) private projectId: number,
    private dialogRef: MatDialogRef<ScenariosCreateComponent>,
    private fb: FormBuilder,
    private aliasDiscovery: AliasDiscovery
  ) { }

  ngOnInit() {

  }

  create() {
    const form = this.form.value;

    if (!this.files.length) {
      const formData = this.buildFormData(form);

      this.onCreate.emit(formData);
      this.dialogRef.close();
    } else {
      const fileReader = new FileReader();
      fileReader.onload = (e) => {
        // let urls: string[] = this.getHarFileUrls(fileReader.result)

        // this.aliasDiscovery.discoverAll(urls).subscribe(aliasMap => {
          const formData = this.buildFormData(form);
          // formData.append('alias_map', JSON.stringify(aliasMap))

          this.onCreate.emit(formData);
          this.dialogRef.close();
        // })
      };
      fileReader.readAsText(this.files[0]);
    }
  }

  buildFormData(form): FormData {
    const formData = new FormData();

    formData.append('name', form.name);
    formData.append('description', form.description);
    formData.append('priority', this.selectedPriority.value.toString());

    if (this.files[0]) {
      formData.append('file', this.files[0]);
    }

    return formData;
  }

  getHarFileUrls(contents): string[] {
    const data = JSON.parse(contents);

    if (!data.log) { return; }
    if (!data.log.entries) { return; }

    const urls: string[] = data.log.entries.map(entry => {
      return entry.request.url;
    });

    return urls;
  }

  onFileSelect(event) {
    console.log(event);
    this.files.push(...event.addedFiles);
  }

  onFileRemove(event) {
    console.log(event);
    this.files.splice(this.files.indexOf(event), 1);
  }

  handlePrioritySelect(priority) {
    this.selectedPriority = priority;
  }
}
