import { Clipboard } from '@angular/cdk/clipboard';
import { Location } from '@angular/common';
import { Component, OnInit } from '@angular/core';
import { FormControl } from '@angular/forms';
import { MatDialog } from '@angular/material/dialog';
import { MatSnackBar } from '@angular/material/snack-bar';
import { ActivatedRoute, ActivatedRouteSnapshot, Router } from '@angular/router';

import { debounceTime } from 'rxjs/operators';

import { fadeInRight400ms, scaleIn400ms, stagger40ms } from '@vex/animations';
import { TableColumn } from '@vex/interfaces/table-column.interface';

import { ScenarioResource } from '@core/http/scenario-resource.service';
import { FileDownload, FileService, UriService  } from '@core/utils';
import { FileDownloadRepresentation, FilterEvent, ScenariosIndexResponse } from '@data/interfaces';
import { Project, Scenario } from '@data/schema';
import { ScenariosCreateComponent } from '@scenarios/components/scenarios-create/scenarios-create.component';
import { ScenariosEditComponent } from '@scenarios/components/scenarios-edit/scenarios-edit.component';

import { ScenarioDataService } from './services/scenario-data.service';
import { ScenariosIndexIcons } from './services/scenarios-index-icons.service';

@Component({
  selector: 'scenarios-index',
  styleUrls: ['./scenarios-index.component.scss'],
  templateUrl: './scenarios-index.component.html',
  animations: [
    stagger40ms,
    scaleIn400ms,
    fadeInRight400ms,
  ],
})
export class ScenariosIndexComponent implements OnInit {
  // Breadcrumb settings
  crumbs = [];

  project: Project;
  indexParams: any = {};

  // Table
  scenarios: Scenario[] = [];
  totalScenarios: number;
  page: number;
  pageSize: number;
  filter: string;

  // search
  // searchCtrl = new FormControl();
  // searchStr$ = this.searchCtrl.valueChanges.pipe(
  //   debounceTime(10)
  // );

  menuOpen = false;

  // activeCategory: 'frequently' | 'starred' | 'all' | 'high' | 'medium' | 'low' | 'none' = 'all';
  tableColumns: TableColumn<Scenario>[];

  constructor(
    public icons: ScenariosIndexIcons,
    private activatedRoute: ActivatedRoute,
    private clipboard: Clipboard,
    private dialog: MatDialog,
    private file: FileService,
    private fileDownload: FileDownload,
    private location: Location,
    private route: ActivatedRoute,
    private router: Router,
    private scenarioDataService: ScenarioDataService,
    private scenarioResource: ScenarioResource,
    private snackbar: MatSnackBar,
    private uri: UriService
  ) { }

  ngOnInit() {
    const routeSnapshot = this.route.snapshot;

    this.project = this.route.snapshot.data.project;

    const scenarios = routeSnapshot.data.scenarios;
    this.scenarios = scenarios.list;
    this.totalScenarios = scenarios.total;

    this.indexParams = { ...routeSnapshot.queryParams };
    this.indexParams.page = this.indexParams.page || 0;
    this.indexParams.size = this.indexParams.size || 20;
    this.page = this.indexParams.page;
    this.pageSize = this.indexParams.size;
    this.filter = this.indexParams.filter;

    this.crumbs.push({ name: this.project.name });
    this.crumbs.push({ name: 'Scenarios' });

    this.tableColumns = this.buildTableColumns();
  }

// API Access

  getScenarios(params: any = this.indexParams) {
    const snapshot: ActivatedRouteSnapshot = this.route.snapshot;
    const project_id = snapshot.queryParams.project_id;

    params.project_id = project_id;

    this.scenarioResource.index(params).subscribe(
      (res: ScenariosIndexResponse) => {
        this.indexParams = params; // Save params for next use
        this.scenarios = res.list;
        this.totalScenarios = res.total;

        this.updateUrlQueryParams(params);
      },
      error => {

      }
    );
  }

  downloadScenario(scenarioId: Scenario['id']) {
    this.scenarioResource.download(scenarioId).subscribe((res: FileDownloadRepresentation) => {
      this.fileDownload.create(res);
    });
  }

  createScenario(data: FormData) {
    const snapshot: ActivatedRouteSnapshot = this.route.snapshot;
    const project_id = snapshot.queryParams.project_id;

    data.append('project_id', project_id);

    this.scenarioResource.create(data).subscribe(
      (res: Scenario) => {
        const clone = this.scenarios.slice();
        clone.unshift(res);
        this.scenarios = clone;
      },
      error => {

      });
  }

  updateScenario(id: Scenario['id'], data) {
    const priority = data.priority;
    const starred = data.starred;
    const description = data.description;
    const name = data.name;
    const body = {description, name, priority, starred};

    this.scenarioResource.update(id, body).subscribe(
      res => {
        const clone = this.scenarios.slice();

        for (let i = 0; i < clone.length; ++i) {
          if (clone[i].id !== id) { continue; }
          clone[i] = new Scenario(res);
        }

        this.scenarios = clone;
      },
      error => {

      });
  }

  deleteScenario(scenarioId: Scenario['id']) {
    this.scenarioResource.destroy(scenarioId).subscribe(res => {
      this.scenarios = this.scenarios.filter((scenario: Scenario) => {
        return scenario.id !== scenarioId;
      });
    });
  }

  sortScenarios(event) {
    const column: string = event.active;
    const direction: string = event.direction; // desc or asc

    if (!direction) {
      delete this.indexParams.sort_by;
      delete this.indexParams.sort_order;
    } else {
      this.indexParams.sort_by = column;
      this.indexParams.sort_order = direction;
    }

    this.getScenarios();
  }

  searchScenarios(queryString: String) {
    const snapshot: ActivatedRouteSnapshot = this.route.snapshot;
    const project_id = snapshot.queryParams.project_id;

    this.indexParams.page = 0;
    this.page = 0;

    if (!queryString.length) {
      delete this.indexParams.q;
    } else {
      this.indexParams.q = queryString;
    }

    this.getScenarios();
  }

  filterScenarios($event: FilterEvent) {
    if (!$event.filter) {
      delete this.indexParams.filter;
    } else {
      this.indexParams.filter = $event.filter;
    }

    this.getScenarios();
  }

// View Access

  viewScenario(scenarioId: Scenario['id']) {
    const scenario: Scenario = this.scenarios.find((candidate: Scenario) => {
      return candidate.id === scenarioId;
    });

    this.scenarioDataService.set(scenario);

    const uri = new this.uri.class(this.location.path());
    const path: string = this.file.join(uri.pathname, scenarioId);

    uri.pathname = path;

    this.router.navigateByUrl(uri.pathname + uri.query);
  }

  toggleScenarioStar(id: Scenario['id']) {
    const scenario = this.scenarios.find(s => s.id === id);

    if (scenario) {
      scenario.starred = !scenario.starred;
      this.updateScenario(id, scenario);
    }
  }

  openCreateDialog() {
    const dialogRef = this.dialog.open(ScenariosCreateComponent, {
      width: '600px',
    });

    const onCreateSub = dialogRef.componentInstance.onCreate.subscribe(($event) => {
      this.createScenario($event);
    });

    dialogRef.afterClosed().subscribe(() => {
      onCreateSub.unsubscribe();
    });
  }

  openEditDialog(scenario: Scenario) {
    const dialogRef = this.dialog.open(ScenariosEditComponent, {
      data: scenario,
      width: '600px',
    });

    const onEditSub = dialogRef.componentInstance.onEdit.subscribe(($event) => {
      this.updateScenario(scenario.id, $event);
    });

    dialogRef.afterClosed().subscribe(() => {
      onEditSub.unsubscribe();
    });
  }

  closeMenu() {
    this.menuOpen = false;
  }

  // For when the screen size is small
  openMenu() {
    this.menuOpen = true;
  }

 /*
  *
  * $event = {
  *   previousPageIndex: 0
  *   pageIndex: 0
  *   pageSize: 50
  *   length: 3
  * }
  *
  */
  handlePaginateChange($event) {
    const curIndex = this.page;
    const curSize = this.pageSize;
    const newIndex = $event.pageIndex;
    const newSize = $event.pageSize;

    if (curSize !== newSize) {
      this.pageSize = newSize;
    }

    if (curIndex != newIndex) {
      this.page = newIndex;
    }

    if (curIndex != newIndex || curSize !== newSize) {
      this.indexParams.page = newIndex;
      this.indexParams.size = newSize;
      this.getScenarios();
    }
  }

  copyMockUrlToClipBoard(scenario) {
    this.clipboard.copy(scenario.mock_url);
    this.snackbar.open('URL copied to clipboard!', 'close', {
      duration: 2000,
    });
  }

// Helpers

  updateUrlQueryParams(newQueryParams) {
    const queryParams = { ...this.indexParams };

    Object.entries(newQueryParams).forEach(([key, value]) => {
      queryParams[key] = value;
      this.indexParams[key] = value;
    });

    const url = this
        .router
        .createUrlTree([], {relativeTo: this.activatedRoute, queryParams})
        .toString();

    this.location.go(url);
  }

  buildTableColumns(): TableColumn<Scenario>[] {
    return [
      // {
      //   label: '',
      //   property: 'selected',
      //   type: 'checkbox',
      //   cssClasses: ['w-6'],
      //   visible: true,
      //   canHide: false,
      // },
      // {
      //   label: '',
      //   property: 'imageSrc',
      //   type: 'image',
      //   cssClasses: ['min-w-9']
      // },
      {
        label: 'Name',
        property: 'name',
        type: 'text',
        cssClasses: ['font-medium'],
        visible: true,
        canHide: true,
      },
      {
        label: 'Requests',
        property: 'request_count',
        type: 'text',
        cssClasses: ['text-secondary'],
        visible: true,
        canHide: true,
      },
      {
        label: 'Description',
        property: 'description',
        type: 'text',
        cssClasses: ['text-secondary'],
        visible: true,
        canHide: true,
      },
      {
        label: 'Created At',
        property: 'created_at',
        type: 'date',
        cssClasses: ['text-secondary'],
        visible: false,
        canHide: true,
      },
      {
        label: 'Mock URL',
        property: 'mock_url',
        type: 'button',
        cssClasses: ['text-secondary'],
        visible: true,
        canHide: true,
        onclick: this.copyMockUrlToClipBoard.bind(this),
        icon: this.icons.icFileCopy,
      },
      {
        label: '',
        property: 'starred',
        type: 'toggleButton',
        cssClasses: ['text-secondary', 'w-10'],
        visible: true,
        canHide: false,
        icon: (scenario) => scenario.starred ? this.icons.icStar : this.icons.icStarBorder,
      },
      {
        label: '',
        property: 'menu',
        type: 'menuButton',
        cssClasses: ['text-secondary', 'w-10'],
        visible: true,
        canHide: false,
      },
    ];
  }
}
