import { Injectable } from '@angular/core';
import { ActivatedRouteSnapshot, Resolve } from '@angular/router';

import { ScenarioResource } from '@core/http/scenario-resource.service';

@Injectable({
  providedIn: 'root',
})
export class ScenariosResolver implements Resolve<any> {

  constructor(
    private scenarioResource: ScenarioResource
  ) { }

  resolve(route: ActivatedRouteSnapshot) {
    return this.scenarioResource.index({
      project_id: route.queryParams.project_id,
    });
  }
}
