import { Injectable } from '@angular/core';

import { DateTime } from 'luxon';

import { Request } from '@data/schema/request';

@Injectable({
  providedIn: 'root',
})
export class RequestCardAdapterService {

  constructor() { }

  createCard(request: Request) {
    return {
      id: request.id,
      title: request.url,
      method: request.method,
      status: request.status,
      position: request.position,
      latency: request.latency,
      dueDate: {
        date: DateTime.fromJSDate(request.createdAt),
        done: false,
      },
      attachments: [],
      comments: [],
      users: [],
      labels: [],
    };
  }
}
